.hero-wrapper {
  position: sticky;
  overflow: hidden;
  top: 0;
  padding-right: var(--space-4);
  padding-left: var(--space-4);

  @media screen and (min-width: $screen-md-min) {
    padding-right: 0;
    padding-left: 0;
  }
}

.hero {
  min-height: 100vh;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 2vw;
  align-items: center;

  @media screen and (min-width: $screen-lg-min) {
    gap: 14vw;
  }
}

.hero-text-wrapper {
  min-width: 280px;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30;
  text-align: center;

  @media screen and (min-width: $screen-md-min) {
    top: 50%;
    font-size: 3.75rem;
  }
}

.hero-title {
  position: relative;
  font-family: var(--font-family-Oswald);
  font-size: var(--space-6);
  font-weight: 800;
  letter-spacing: 0.025em;
  color: var(--color-primary-light);
  -webkit-text-stroke: 0.0625rem #000;

  @media screen and (min-width: $screen-md-min) {
    font-size: var(--space-8);
  }

  @media screen and (min-width: $screen-lg-min) {
    font-size: 3.75rem;
  }
}

.hero-title span {
  display: block;
  color: var(--color-main-text);
}

.hero-subtitle {
  font-family: var(--font-family-Oswald);
  font-size: var(--space-4);
  font-weight: var(--text-light);
  color: var(--color-main-text);
  letter-spacing: 0.3em;
}

.hero-column {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 20rem;

  @media screen and (min-width: $screen-md-min) {
    height: 100vh;;
  }

}

.left-column-up,
.right-column-up {
  position: relative;
  z-index: 20;
  animation: moveUp 40s linear infinite;
}

@keyframes moveUp {
  0%, 100% {
    transform: translateY(100%);
  }
  50% {
    transform: translateY(-100%);
  }
}

.left-column-down,
.right-column-down {
  position: relative;
  z-index: 10;
  animation: moveDown 40s linear infinite;
}

@keyframes moveDown {
  0%, 100% {
    transform: translateY(-100%);
  }
  50% {
    transform: translateY(100%);
  }
}
.right-column-up {
  left: var(--space-8);

  @media screen and (min-width: $screen-md-min) {
    left: var(--space-24);
  }

  @media screen and (min-width: $screen-lg-min) {
    left: 17rem;
  }

}

.left-column-down {
  left: -2rem;

  @media screen and (min-width: $screen-md-min) {
    left: -6rem;
  }

  @media screen and (min-width: $screen-lg-min) {
    left: -17rem;
  }
}
