html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

fieldset {
  margin-inline: 0;
  padding-block: 0;
  padding-inline: 0;
  display: block;
}

:root {
  --font-family-Oswald: "Oswald", sans-serif;
  --font-family-Montserrat: "Montserrat", sans-serif;
  --color-base-white: #fff;
  --color-base-black: #000;
  --color-primary-light: #f5f5f5;
  --color-secondary: #2b2b2b;
  --color-accent: #ffd140;
  --color-main-text: #131412;
  --color-dark-text: #151512;
  --color-light: #f3f3f3;
  --color-background-mobile-menu: #e7e7e7;
  --color-background-contacts: #e9e9e9;
  --color-copyright: #c2c2c2;
  --space-1: .25rem;
  --space-2: .5rem;
  --space-3: .75rem;
  --space-4: 1rem;
  --space-5: 1.25rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-10: 2.5rem;
  --space-12: 3rem;
  --space-16: 4rem;
  --space-20: 5rem;
  --space-24: 6rem;
  --space-32: 8rem;
  --space-40: 10rem;
  --space-48: 12rem;
  --space-56: 14rem;
  --space-64: 16rem;
  --text-extralight: 200;
  --text-light: 300;
  --text-regular: 400;
  --text-medium: 500;
  --text-semibold: 600;
  --text-bolt: 700;
  --line-height-text-xs: 1.125rem;
  --line-height-text-sm: 1.25rem;
  --line-height-text-md: 1.5rem;
  --line-height-text-lg: 1.75rem;
  --line-height-text-xl: 1.875rem;
  --font-size-text-xs: .75rem;
  --font-size-text-sm: .875rem;
  --font-size-text-md: 1rem;
  --font-size-text-lg: 1.125rem;
  --font-size-text-xl: 1.25rem;
  --font-size-display-xs: 1.5rem;
  --font-size-display-sm: 1.875rem;
  --font-size-display-md: 2.25rem;
  --font-size-display-lg: 3rem;
  --font-size-display-xl: 4.5rem;
  --font-size-display-2xl: 3.75rem;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  font-family: var(--font-family-Montserrat);
  font-size: 16px;
  font-weight: var(--text-regular);
  color: var(--color-base-black);
  background-color: var(--color-primary-light);
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

svg {
  display: inline-block;
}

img {
  max-width: 100%;
  display: block;
}

.container-md {
  max-width: 48rem;
  padding-right: var(--space-4);
  padding-left: var(--space-4);
}

@media screen and (min-width: 768px) {
  .container-md {
    padding-right: var(--space-5);
    padding-left: var(--space-5);
  }
}

.container-lg {
  max-width: 64rem;
  padding-right: var(--space-4);
  padding-left: var(--space-4);
}

@media screen and (min-width: 768px) {
  .container-lg {
    padding-right: var(--space-8);
    padding-left: var(--space-8);
  }
}

.container-xl {
  max-width: 86rem;
  padding-right: var(--space-4);
  padding-left: var(--space-4);
}

@media screen and (min-width: 768px) {
  .container-xl {
    padding-right: var(--space-8);
    padding-left: var(--space-8);
  }
}

.mobile-header {
  z-index: 1;
  width: 100vw;
  background-color: var(--color-light);
  padding: 20px 16px 40px 24px;
  position: fixed;
  box-shadow: 0 0 20px #0000001a;
}

@media screen and (min-width: 768px) {
  .mobile-header {
    display: none;
  }
}

.mobile-header-container {
  z-index: 100;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.mobile-nav-container {
  z-index: 90;
  width: 100vw;
  height: 100vh;
  background-color: var(--color-background-mobile-menu);
  padding: 125px 16px 40px;
  transition: transform 1s cubic-bezier(.45, .05, .55, .95);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

@media screen and (min-width: 1280px) {
  .mobile-nav-container {
    margin-left: auto;
  }
}

.mobile-nav-item {
  font-size: var(--font-size-text-xl);
  font-weight: var(--text-medium);
  text-transform: uppercase;
  display: block;
  position: relative;
}

.mobile-nav-item:after {
  content: "";
  width: 0;
  height: 1px;
  background-color: var(--color-main-text);
  transition: width .5s;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.mobile-nav-item:hover:after {
  width: 100%;
}

.mobile-nav-item:first-child {
  margin-bottom: 2.1875rem;
}

.mobile-nav-button {
  font-size: var(--font-size-text-xl);
  font-weight: var(--text-medium);
  text-transform: uppercase;
}

.burger-menu {
  display: block;
}

.is-open .burger-menu, .burger-menu-cross {
  display: none;
}

.is-open .burger-menu-cross {
  display: block;
}

.is-open .mobile-nav-container {
  transform: translateY(0);
}

.header {
  z-index: 10;
  background-color: #0000;
  padding: 30px 64px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

@media screen and (max-width: 767px) {
  .header {
    display: none;
  }
}

.header-logo {
  position: absolute;
  top: 26%;
  right: 50%;
  transform: translateX(50%);
}

.header-nav-list {
  font-size: var(--font-size-text-lg);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav-item {
  display: block;
  position: relative;
}

.header-nav-item:hover:after {
  width: 100%;
}

.header-nav-item:after {
  content: "";
  width: 0;
  height: 1px;
  background-color: var(--color-main-text);
  transition: width .5s;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.hero-wrapper {
  padding-right: var(--space-4);
  padding-left: var(--space-4);
  position: sticky;
  top: 0;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .hero-wrapper {
    padding-left: 0;
    padding-right: 0;
  }
}

.hero {
  min-height: 100vh;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  align-items: center;
  gap: 2vw;
  display: grid;
}

@media screen and (min-width: 1280px) {
  .hero {
    gap: 14vw;
  }
}

.hero-text-wrapper {
  min-width: 280px;
  z-index: 30;
  text-align: center;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .hero-text-wrapper {
    font-size: 3.75rem;
    top: 50%;
  }
}

.hero-title {
  font-family: var(--font-family-Oswald);
  font-size: var(--space-6);
  letter-spacing: .025em;
  color: var(--color-primary-light);
  -webkit-text-stroke: .0625rem #000;
  font-weight: 800;
  position: relative;
}

@media screen and (min-width: 768px) {
  .hero-title {
    font-size: var(--space-8);
  }
}

@media screen and (min-width: 1280px) {
  .hero-title {
    font-size: 3.75rem;
  }
}

.hero-title span {
  color: var(--color-main-text);
  display: block;
}

.hero-subtitle {
  font-family: var(--font-family-Oswald);
  font-size: var(--space-4);
  font-weight: var(--text-light);
  color: var(--color-main-text);
  letter-spacing: .3em;
}

.hero-column {
  flex-direction: column;
  gap: 20rem;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .hero-column {
    height: 100vh;
  }
}

.left-column-up, .right-column-up {
  z-index: 20;
  animation: 40s linear infinite moveUp;
  position: relative;
}

@keyframes moveUp {
  0%, 100% {
    transform: translateY(100%);
  }

  50% {
    transform: translateY(-100%);
  }
}

.left-column-down, .right-column-down {
  z-index: 10;
  animation: 40s linear infinite moveDown;
  position: relative;
}

@keyframes moveDown {
  0%, 100% {
    transform: translateY(-100%);
  }

  50% {
    transform: translateY(100%);
  }
}

.right-column-up {
  left: var(--space-8);
}

@media screen and (min-width: 768px) {
  .right-column-up {
    left: var(--space-24);
  }
}

@media screen and (min-width: 1280px) {
  .right-column-up {
    left: 17rem;
  }
}

.left-column-down {
  left: -2rem;
}

@media screen and (min-width: 768px) {
  .left-column-down {
    left: -6rem;
  }
}

@media screen and (min-width: 1280px) {
  .left-column-down {
    left: -17rem;
  }
}

.about-wrapper {
  z-index: 10;
  padding-top: var(--space-20);
  padding-bottom: var(--space-32);
  background-color: var(--color-accent);
  position: relative;
  top: 0;
}

.about {
  gap: var(--space-5);
  flex-direction: column;
  display: flex;
}

@media screen and (min-width: 768px) {
  .about {
    flex-direction: row;
  }
}

.about-title {
  margin-bottom: var(--space-8);
  font-family: var(--font-family-Oswald);
  font-size: var(--space-10);
  font-weight: var(--text-medium);
  letter-spacing: .0406rem;
}

@media screen and (min-width: 1280px) {
  .about-title {
    margin-bottom: var(--space-5);
    font-size: var(--space-12);
  }
}

.about-subtitle {
  margin-bottom: var(--space-5);
  font-family: var(--font-family-Montserrat);
  font-weight: var(--text-extralight);
}

.about-description p {
  margin-bottom: var(--space-5);
  font-size: 1.125rem;
}

@media screen and (min-width: 1280px) {
  .about-description p {
    max-width: 36rem;
  }
}

.about-button {
  margin-top: var(--space-6);
  padding: 1.125rem var(--space-12);
  font-family: var(--font-family-Oswald);
  font-size: var(--space-5);
  font-weight: var(--text-medium);
  color: var(--color-base-white);
  background-color: var(--color-secondary);
  cursor: pointer;
  border: .125rem solid #0000;
  transition: border .5s, color .5s, background-color .5s;
}

.about-button:hover {
  color: var(--color-main-text);
  background-color: var(--color-accent);
  border: .125rem solid var(--color-main-text);
}

.about-websites {
  gap: var(--space-5);
  margin-top: var(--space-10);
  flex-direction: column;
  display: flex;
}

@media screen and (min-width: 768px) {
  .about-websites {
    max-width: 36rem;
    margin-top: var(--space-32);
  }
}

.about-item {
  cursor: pointer;
  height: auto;
  transition: height .5s;
  position: relative;
  overflow: hidden;
}

.about-item:before {
  content: "";
  width: 100%;
  height: var(--space-2);
  background: url("about-border-top.42d6f683.svg") 0 0 / cover no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}

.about-item .about-info.is-open {
  height: 7rem;
}

@media screen and (min-width: 768px) {
  .about-item .about-info.is-open {
    height: 6rem;
  }
}

.about-number {
  font-family: var(--font-family-Oswald);
  font-size: var(--space-6);
  font-weight: var(--text-medium);
  letter-spacing: .0156rem;
  position: absolute;
  top: .625rem;
  left: 1.75rem;
}

@media screen and (min-width: 768px) {
  .about-number {
    font-size: var(--space-5);
    left: 2rem;
  }
}

.about-item h3 {
  padding: .625rem 1.6875rem 0 var(--space-16);
  font-family: var(--font-family-Oswald);
  font-size: var(--space-6);
  font-weight: var(--text-medium);
  letter-spacing: .0375rem;
  text-transform: uppercase;
}

@media screen and (min-width: 768px) {
  .about-item h3 {
    font-size: var(--space-5);
  }
}

.about-info {
  height: 0;
  padding: .625rem var(--space-5) var(--space-3) 1.875rem;
  color: var(--color-main-text);
  opacity: 0;
  visibility: hidden;
  transition: all .5s;
}

.about-info:after {
  content: "";
  width: 100%;
  height: .5625rem;
  background: url("about-border-bottom.1d1449e0.svg") 0 100% / cover no-repeat;
  position: absolute;
  bottom: 0;
  left: 0;
}

.about-info.is-open {
  height: auto;
  opacity: 1;
  visibility: visible;
  padding-top: .5625rem;
  padding-bottom: .75rem;
}

.modal-overlay {
  z-index: 50;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: #00000034;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal.closed {
  transform: scale(0)translate(-50%, -50%);
}

.modal-close-button {
  top: var(--space-4);
  right: var(--space-4);
  color: #fff;
  transition: color .25s linear;
  position: absolute;
}

.modal-close-button:hover {
  color: var(--color-accent);
}

.modal {
  z-index: 100;
  width: 95%;
  max-width: 32rem;
  border: 1px solid var(--color-base-white);
  background-color: var(--color-secondary);
  transform-origin: 0 0;
  transition: all .5s;
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: scale(1)translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 43.75rem;
  }
}

.modal-content {
  padding: 5.25rem var(--space-5);
}

.modal-title {
  text-align: center;
  margin-bottom: var(--space-4);
  font-family: var(--font-family-Oswald);
  font-size: 2.275rem;
  font-weight: var(--text-regular);
  letter-spacing: .0406rem;
  color: var(--color-base-white);
}

.modal-subtitle {
  text-align: center;
  margin-bottom: var(--space-10);
  font-family: var(--font-family-Oswald);
  font-size: var(--space-5);
  font-weight: var(--text-light);
  letter-spacing: .0406rem;
  color: var(--color-base-white);
}

@media screen and (min-width: 768px) {
  .form {
    max-width: 21rem;
    text-align: center;
    margin: auto;
  }
}

.form-group:not(:last-child) {
  margin-bottom: 1.875rem;
}

.form > .form-group:not(:first-child):not(:last-child) {
  margin-bottom: var(--space-10);
}

.form-group {
  position: relative;
}

.form-input {
  width: 100%;
  background-color: #0000;
  border: 1px solid #0000;
  border-bottom-color: #fff;
  border-radius: 0;
  outline: none;
}

.form-input::placeholder {
  color: #0000;
}

.form-label {
  color: #999;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.form-input:not(:placeholder-shown) + .form-label, .form-input:focus + .form-label {
  transform: translateY(-1.125rem);
}

.modal-send-button {
  width: 14.125rem;
  padding: var(--space-3) 1.75rem;
  font-family: var(--font-family-Oswald);
  font-size: var(--space-5);
  letter-spacing: .0406rem;
  color: var(--color-secondary);
  background-color: var(--color-accent);
  cursor: pointer;
  border: 1px solid #0000;
  transition: background-color .5s;
}

.modal-send-button:hover {
  background-color: var(--color-base-white);
}

.closed {
  pointer-events: none;
  opacity: 0;
}

.projects-wrapper {
  z-index: 20;
  background-color: var(--color-secondary);
  padding-top: 3.75rem;
  padding-bottom: 3.75rem;
  position: relative;
}

@media screen and (min-width: 768px) {
  .projects-body {
    gap: var(--space-5);
